<template>
	<div>
		<!-- -->
		<div style="border: 1px solid #ccc">
			<Toolbar style="border-bottom: 1px solid #ccc" :editor="editorRef" :defaultConfig="toolbarConfig"
				:mode="mode" />
			<Editor style="height: 500px; overflow-y: hidden;" v-model="valueHtml" :defaultConfig="editorConfig"
				:mode="mode" @onCreated="handleCreated" @onChange="handleChange" />
		</div>
	</div>
</template>
<script setup>
	import '@wangeditor/editor/dist/css/style.css' //  css

	import {
		onBeforeUnmount,
		ref,
		shallowRef,
		onMounted,
		getCurrentInstance,
		defineEmits
	} from 'vue'
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	import {
		Editor,
		Toolbar
	} from '@wangeditor/editor-for-vue'
	const emit = defineEmits(['change'])
	//  shallowRef
	const editorRef = shallowRef()
	
	//  HTML
	const valueHtml = ref('')
	
	//  ajax 
	onMounted(() => {
		setTimeout(() => {
			valueHtml.value = ''
		}, 1500)
	})
	const mode = 'default' //  'simple'
	const toolbarConfig = {
		excludeKeys: ['fullScreen', 'group-video', 'insertLink', 'insertImage']
	}
	const editorConfig = {
		placeholder: '...',
		fontSize:['12px','14px','16px'],
		MENU_CONF: {
			uploadImage: {
				server: context?.$config.name + '/file/upload',
				fieldName: 'file',
				//  2M
				maximgSize: 10 * 1024 * 1024, // 10M
				//  100
				maxNumberOfimgs: 10,
				//  ['image/*']  []
				allowedimgTypes: ['image/*'],
				//  token  formData 
				meta: {
					// token: 'xxx',
					// otherKey: 'yyy'
					// img:''
				},
				//  meta  url  false
				metaWithUrl: false,
	
				//  http  header
				headers: {
					'Token':context?.$toolUtil.storageGet('Token')
				},
	
				//  cookie  false
				withCredentials: true,
	
				//  10 
				timeout: 10 * 1000, //10 
	
				// 
				onBeforeUpload(imgs) {
					context?.$toolUtil.message(',','warning')
					return imgs;
				},
				// 
				customInsert(res, insertFn) {
					// onSuccessonFailed,
	
					if (res.code === 0) {
						context?.$toolUtil.message('','success')
					} else {
						context?.$toolUtil.message('','error')
					}
					//  res  url alt href 
					insertFn(context?.$config.url + 'file/' + res.file);
					// console.log(res, "res.data")
				},
	
				// 
				onSuccess(img, res) {
				},
	
				// 
				onFailed(img, res) {
				},
	
				// 
				onProgress(progress) {
					// console.log('progress', progress);
					// progress  0-100 
				},
	
				//  timeout 
				onError(img, err, res) {
					// console.log(`${img.name} `, err, res);
				}
			}
		}
	}
	
	// 
	onBeforeUnmount(() => {
		const editor = editorRef.value
		if (editor == null) return
		editor.destroy()
	})
	const handleChange=(e)=>{
		emit('change',editorRef.value.getHtml())
	}
	const handleCreated = (editor) => {
		editorRef.value = editor //  editor 
	}
</script>

<style>
	.editor {
		line-height: normal !important;
	}

	.ql-snow .ql-tooltip[data-mode="link"]::before {
		content: ":";
	}

	.ql-snow .ql-tooltip.ql-editing a.ql-action::after {
		border-right: 0px;
		content: "";
		padding-right: 0px;
	}

	.ql-snow .ql-tooltip[data-mode="video"]::before {
		content: ":";
	}

	.ql-container {
		height: 400px;
	}

	.ql-snow .ql-picker.ql-size .ql-picker-label::before,
	.ql-snow .ql-picker.ql-size .ql-picker-item::before {
		content: "14px";
	}

	.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="small"]::before,
	.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="small"]::before {
		content: "10px";
	}

	.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="large"]::before,
	.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="large"]::before {
		content: "18px";
	}

	.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="huge"]::before,
	.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="huge"]::before {
		content: "32px";
	}

	.ql-snow .ql-picker.ql-header .ql-picker-label::before,
	.ql-snow .ql-picker.ql-header .ql-picker-item::before {
		content: "";
	}

	.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="1"]::before,
	.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="1"]::before {
		content: "1";
	}

	.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="2"]::before,
	.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="2"]::before {
		content: "2";
	}

	.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="3"]::before,
	.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="3"]::before {
		content: "3";
	}

	.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="4"]::before,
	.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="4"]::before {
		content: "4";
	}

	.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="5"]::before,
	.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="5"]::before {
		content: "5";
	}

	.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="6"]::before,
	.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="6"]::before {
		content: "6";
	}

	.ql-snow .ql-picker.ql-font .ql-picker-label::before,
	.ql-snow .ql-picker.ql-font .ql-picker-item::before {
		content: "";
	}

	.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="serif"]::before,
	.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="serif"]::before {
		content: "";
	}

	.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="monospace"]::before,
	.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="monospace"]::before {
		content: "";
	}
</style>
