<template>
	<div class="app-contain" :style='{"border":"0","padding":"20px 10%","margin":"0 auto","color":"#666","background":"#fff","width":"100%","fontSize":"14px","position":"relative"}'>
		<div class="back_view" v-if="centerType">
			<el-button class="back_btn" @click="backClick" type="primary"></el-button>
		</div>
		<div class="section_title">
			{{myType?'':formName}}
		</div>
		<el-form :model="searchQuery" class="list_search">
			<div class="search_view">
				<div class="search_label">
					
				</div>
				<div class="search_box">
					<el-input class="search_inp" v-model="searchQuery.title" placeholder="" size="small" clearable>
					</el-input>
				</div>
			</div>
			<div class="search_btn_view">
				<el-button class="search_btn" type="primary" @click="searchClick"></el-button>
				<el-button class="add_btn" @click="addClick" v-if="!myType&&btnAuth('forum','')" type="success"></el-button>
			</div>
		</el-form>
		<div class="forum_list">
			<div class="forum_item" v-for="(item,index) in list" :key="index" @mouseenter="forumEnter(index)"
				@mouseleave="forumLeave" @click.stop="detailClick(item.id)">
				<div class="forum_item_left">
					<span class="forum_item_title">{{item.title}}</span>
					<span class="forum_item_name">({{item.username}})</span>
				</div>
				<div class="forum_item_btn_box" v-if="item.userid==userid&&forumShowIndex==index&&(btnAuth('forum','')||btnAuth('forum',''))">
					<el-button class="forum_edit_btn" v-if="btnAuth('forum','')" type="primary" @click.stop="editClick(item.id)"></el-button>
					<el-button class="forum_del_btn" v-if="btnAuth('forum','')" type="danger" @click.stop="delClick(item.id)"></el-button>
				</div>
				<div class="forum_time" v-else>{{item.addtime}}</div>
			</div>
			<el-pagination
				background 
				:layout="layouts.join(',')"
				:total="total" 
				:page-size="listQuery.limit"
				prev-text="<"
				next-text=">"
				:hide-on-single-page="false"
				:style='{"border":"0px solid #eee","padding":"4px 0","margin":"10px 0 20px","whiteSpace":"nowrap","color":"#333","textAlign":"center","flexWrap":"wrap","background":"none","display":"flex","width":"100%","fontWeight":"500","justifyContent":"center"}'
				@size-change="sizeChange"
				@current-change="currentChange" 
				@prev-click="prevClick"
				@next-click="nextClick"  />
		</div>
		
		<el-dialog v-model="formVisible" :title="formTitle" width="70%" destroy-on-close>
			<el-form class="add_form" :model="form" label-width="120px" :rules="rules" ref="formRef">
				<el-row>
					<el-col :span="24">
						<el-form-item label="" prop="title">
							<el-input class="list_inp" v-model="form.title" placeholder=""></el-input>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="">
							<el-radio-group class="list_radio" v-model="form.isdone">
								<el-radio label=""></el-radio>
								<el-radio label=""></el-radio>
							</el-radio-group>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="content">
							<editor :value="form.content" placeholder=""
								class="list_editor" @change="contentChange"></editor>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer>
				<span class="formModel_btn_box">
					<el-button class="formModel_cancel" @click="formVisible=false"></el-button>
					<el-button class="formModel_confirm" type="primary" @click="save">{{form.id?'':''}}</el-button>
				</span>
			</template>
		</el-dialog>
		<el-dialog v-model="detailVisible" :title="''" width="70%" destroy-on-close>
			<div class="forum_detail">
				<div class="forum_title">
					{{detailForm.title}}
				</div>
				<div class="forum_info">
					<span class="forum_name">{{detailForm.username}}</span>
					<span class="forum_time">{{detailForm.addtime}}</span>
				</div>
				<el-divider />
				<div class="forum_content" v-html="detailForm.content"></div>
			</div>
			<el-card class="forum_comment_box">
				<el-divider class="comment_divider" content-position="center"></el-divider>
				<div class="comment_add_box">
					<el-button class="comment_add" @click="commentClick" type="success"></el-button>
				</div>
				<div class="forum_comment_list">
					<div class="forum_comment" v-for="(item,index) in detailForm.childs" :key="index"
						@mouseenter="commentEnter(index)" @mouseleave="commentLeave">
						<div class="forum_comment_user">
							<img v-if="item.avatarurl" class="forum_comment_user_avatar"
								:src="$config.url + item.avatarurl.split(',')[0]" alt="">
							<img v-else class="forum_comment_user_avatar" src="@/assets/avatar.png" alt="">
							<span class="forum_comment_username">{{item.username}}</span>
						</div>
						<div class="forum_comment_content" v-html="item.content"></div>
						<div class="forum_comment_reply_view">
							<div class="forum_comment_del" v-if="commentShowIndex==index&&item.userid==userid"
								@click="commentDelClick(item.id)"></div>
							<div class="forum_comment_reply" v-if="commentShowIndex==index"
								@click="commentReplyClick(item.id)"></div>
						</div>
						<div class="forum_comment_second_list" v-if="item.childs">
							<el-divider class="forum_comment_second_divider" content-position="center"></el-divider>
							<div class="forum_comment_second" v-for="(items,indexs) in item.childs" :key="indexs"
								@mouseenter="commentEnter1(indexs)" @mouseleave="commentLeave1">
								<div class="forum_comment_second_user">
									<img v-if="item.avatarurl" class="forum_comment_second_user_avatar"
										:src="$config.url + item.avatarurl.split(',')[0]" alt="">
									<img v-else class="forum_comment_second_user_avatar" src="@/assets/avatar.png" alt="">
									<span class="forum_comment_second_username">{{items.username}}</span>
								</div>
								<div class="forum_comment_second_content" v-html="items.content"></div>
								<div class="forum_comment_second_reply_view">
									<div class="forum_comment_second_del" 
										v-if="commentShowIndex==index&&item.userid==userid&&commentShowIndex1==indexs"
										@click="commentDelClick(items.id)"></div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</el-card>
		</el-dialog>
		<el-dialog v-model="commentVisible" :title="''" width="50%" destroy-on-close>
			<el-form class="add_form" :model="commentForm" label-width="120px">
				<el-row>
					<el-col :span="24">
						<el-form-item prop="content" label="">
							<editor :value="commentForm.content" placeholder=""
								class="list_editor" @change="commentChange"></editor>
						</el-form-item>
					</el-col>
				</el-row>
				
			</el-form>
			<template #footer>
				<span class="formModel_btn_box">
					<el-button class="formModel_cancel" @click="commentVisible=false"></el-button>
					<el-button class="formModel_confirm" type="primary" @click="commentSave"></el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>

<script setup>
	import {
		ref,
		nextTick,
		getCurrentInstance
	} from 'vue';
	import {
		ElMessageBox
	} from 'element-plus'
	import {
		useRoute,
		useRouter
	} from 'vue-router'
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	//
	const tableName = 'forum'
	const formName = ''
	const router = useRouter()
	const route = useRoute()
	//
	//
	const btnAuth = (e, a) => {
		return context?.$toolUtil.isAuth(e, a)
	}
	const list = ref([])
	const listLoading = ref(false)
	const listQuery = ref({
		page: 1,
		limit: 20,
		isdone: '',
		sort: 'addtime',
		order: 'desc'
	})
	const myListQuery = ref({
		page: 1,
		limit: 20,
		parentid: 0,
		sort: 'addtime',
		order: 'desc'
	})
	const searchQuery = ref({})
	//
	const layouts = ref(["prev","pager","next"])
	const total = ref(0)
	const sizeChange = (size) => {
		listQuery.value.limit = size
		getList()
	}
	const currentChange = (page) => {
		listQuery.value.page = page
		getList()
	}
	const prevClick = () => {
		listQuery.value.page = listQuery.value.page - 1
		getList()
	}
	const nextClick = () => {
		listQuery.value.page = listQuery.value.page + 1
		getList()
	}
	//
	const searchClick = () => {
		listQuery.value.page = 1
		getList()
	}
	const getList = () => {
		listLoading.value = true
		let params = myType.value ? JSON.parse(JSON.stringify(myListQuery.value)) : JSON.parse(JSON.stringify(listQuery
			.value))
		if (searchQuery.value.title && searchQuery.value.title != '') {
			params['title'] = `%${searchQuery.value.title}%`
		}
		context?.$http({
			url: `forum/${myType.value?'page':'flist'}`,
			method: 'get',
			params: params
		}).then(res => {
			listLoading.value = false
			list.value = res.data.data.list
			total.value = res.data.data.total
		})
	}
	const userid = ref(0)
	const form = ref({
		title: '',
		isdone: '',
		content: '',
		parentid: 0,
		userid: userid.value,
		username: context?.$toolUtil.storageGet('frontName')
	})
	const rules = ref({
		title: [{
			required: true,
			message: '',
			trigger: 'blur'
		}, ],
		content: [{
			required: true,
			message: '',
			trigger: 'blur'
		}, ],
	})
	const formRef = ref(null)
	const formVisible = ref(false)
	const formTitle = ref('')
	const forumShowIndex = ref(-1)
	//
	const forumEnter = (index) => {
		forumShowIndex.value = index
	}
	const forumLeave = () => {
		forumShowIndex.value = -1
	}
	const resetForm = () => {
		form.value = {
			title: '',
			isdone: '',
			content: '',
			parentid: 0,
			userid: userid.value,
			username: context?.$toolUtil.storageGet('frontName')
		}
	}
	//
	const addClick = () => {
		resetForm()
		formTitle.value = ''
		formVisible.value = true
	}
	//
	const editClick = (id = null) => {
		if (id) {
			context?.$http({
				url: `forum/detail/${id}`,
				method: 'get'
			}).then(res => {
				form.value = res.data.data
				formTitle.value = ''
				formVisible.value = true
			})
		}

	}
	//
	const contentChange = (e)=>{
		form.value.content = e
	}
	//
	const myType = ref(false)
	//
	const centerType = ref(false)
	//
	const backClick = () => {
		router.push(`/index/${context?.$toolUtil.storageGet('frontSessionTable')}Center`)
	}
	//
	const init = () => {
		if (route.query.centerType) {
			centerType.value = true
		}
		if (route.query.myType) {
			myType.value = true
		}
		userid.value = context?.$toolUtil.storageGet('userid')
		getList()
	}
	const detailVisible = ref(false)
	const detailForm = ref({})
	//
	const detailClick = (id = null) => {
		if (id) {
			context?.$http({
				url: `forum/list/${id}`,
				method: 'get'
			}).then(res => {
				detailForm.value = res.data.data
				detailVisible.value = true
			})
		}
	}
	//
	const delClick = (id = null) => {
		if (id) {
			ElMessageBox.confirm(``, '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning',
			}).then(() => {
				context?.$http({
					url: `forum/delete`,
					method: 'post',
					data: [id]
				}).then(res => {
					context?.$toolUtil.message('', 'success', () => {
						getList()
					})
				})
			})
		}
	}
	//
	const save = () => {
		formRef.value.validate((valid) => {
			if (valid) {
				context?.$http({
					url: `forum/${form.value.id?'update':'add'}`,
					method: 'post',
					data: form.value
				}).then(res => {
					context?.$toolUtil.message(`${form.value.id?'':''}`, 'success', () => {
						getList()
						formVisible.value = false
					})
				})
			}
		})
	}
	//
	const commentForm = ref({
		content: '',
		parentid: '',
		userid: '',
		username: '',
		avatarurl: '',
	})
	const commentVisible = ref(false)
	//
	const resetCommentForm = () => {
		commentForm.value = {
			content: '',
			parentid: '',
			userid: context?.$toolUtil.storageGet('userid'),
			username: context?.$toolUtil.storageGet('frontName'),
			avatarurl: context?.$toolUtil.storageGet('headportrait') ? context?.$toolUtil.storageGet('headportrait') : '',
		}
	}
	//
	const commentClick = () => {
		resetCommentForm()
		commentForm.value.parentid = detailForm.value.id
		commentVisible.value = true
	}
	//
	const commentShowIndex = ref(-1)
	const commentShowIndex1 = ref(-1)
	const commentEnter = (index) => {
		commentShowIndex.value = index
	}
	const commentLeave = () => {
		commentShowIndex.value = -1
	}
	const commentEnter1 = (index) => {
		commentShowIndex1.value = index
	}
	const commentLeave1 = () => {
		commentShowIndex1.value = -1
	}
	//
	const commentChange = (e)=>{
		commentForm.value.content = e
	}
	//
	const commentSave = () => {
		if(commentForm.value.content==''){
			context?.$toolUtil.message(``, 'error')
			return false
		}
		let sensitiveWords = "";
		let sensitiveWordsArr = [];
		if(sensitiveWords) {
			sensitiveWordsArr = sensitiveWords.split(",");
		}
		for(var i=0; i<sensitiveWordsArr.length; i++){
			//
			var reg = new RegExp(sensitiveWordsArr[i],"g");
			//
			if (commentForm.value.content.indexOf(sensitiveWordsArr[i]) > -1) {
				//  **
				commentForm.value.content = commentForm.value.content.replace(reg,"**");
			}
		}
		context?.$http({
			url: 'forum/add',
			method: 'post',
			data: commentForm.value
		}).then(res => {
			context?.$toolUtil.message(``, 'success', () => {
				detailClick(detailForm.value.id)
				commentVisible.value = false
			})
		})
	}
	//
	const commentDelClick = (id = null) => {
		if (id) {
			ElMessageBox.confirm(``, '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning',
			}).then(() => {
				context?.$http({
					url: `forum/delete`,
					method: 'post',
					data: [id]
				}).then(res => {
					context?.$toolUtil.message('', 'success', () => {
						detailClick(detailForm.value.id)
					})
				})
			})
		}
	}
	//
	const commentReplyClick = (id = null) => {
		if (id) {
			resetCommentForm()
			commentForm.value.parentid = id
			commentVisible.value = true
		}
	}
	init()
</script>

<style lang="scss" scoped>
	// 
	.back_view {
		border-radius: 0px;
		padding: 0;
		margin: 10px auto;
		background: none;
		display: block;
		width: 100%;
		text-align: right;
		// 
		.back_btn {
			border: 1px solid #eee;
			cursor: pointer;
			border-radius: 2px;
			padding: 0 30px;
			outline: none;
			color: #666;
			background: #f9f9f9;
			width: auto;
			font-size: 14px;
			height: 32px;
		}
		// -
		.back_btn:hover {
		}
	}
	//
	.list_search {
		border: 0px solid #ddd;
		padding: 0;
		margin: 20px auto;
		background: none;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: center;
		.search_view {
			margin: 0 10px 0 0;
			display: flex;
			align-items: center;
			.search_label {
				margin: 0 10px 0 0;
				color: #666;
				font-weight: 500;
				display: inline-block;
				font-size: 14px;
				line-height: 40px;
				height: 40px;
			}
			.search_box {
				display: flex;
				width: calc(100% - 100px);
				// 
				:deep(.search_inp) {
					border: 1px solid #eee;
					box-shadow: 0 0 0px rgba(85, 255, 255, 0.5);
					padding: 0 10px;
					outline: 0px solid #efefef;
					background: #fff;
					width: 100%;
					outline-offset: 0px;
					line-height: 36px;
					box-sizing: border-box;
					min-width: 200px;
					height: 36px;
					//
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
			}
		}
		.search_btn_view {
			margin: 20px 0;
			display: flex;
			// 
			.search_btn {
				border: 0;
				cursor: pointer;
				border-radius: 0px;
				padding: 0 20px;
				outline: none;
				margin: 0 10px 0 0;
				color: #fff;
				background: #0d6392;
				width: auto;
				font-size: 14px;
				height: 36px;
			}
			// -
			.search_btn:hover {
				opacity: 0.8;
			}
			// 
			.add_btn {
				border: 0;
				cursor: pointer;
				border-radius: 0px;
				padding: 0 20px;
				margin: 0 10px 0 0;
				outline: none;
				color: #333;
				background: #0058ab30;
				width: auto;
				font-size: 14px;
				height: 36px;
			}
			// -
			.add_btn:hover {
				opacity: 0.8;
			}
		}
	}
	// 
	.forum_list {
		border: 0px solid #eee;
		border-radius: 0px;
		padding: 0 0 20px 2%;
		margin: 0px auto 40px;
		overflow: hidden;
		background: none;
		width: calc(100% - 0px);
		box-sizing: border-box;
		// item
		.forum_item {
			cursor: pointer;
			padding: 0 16px;
			margin: 0 2% 0px 0;
			color: #666;
			display: flex;
			border-color: #0058ab20;
			box-sizing: border-box;
			float: left;
			border-radius: 0px;
			background: #fff;
			width: 100%;
			justify-content: space-between;
			border-width: 0 0 1px;
			align-items: center;
			border-style: solid;
			height: 44px;
			// item
			.forum_item_left {
				display: flex;
				align-items: center;
				// 
				.forum_item_title {
					font-weight: 600;
					font-size: 14px;
				}
				// 
				.forum_item_name {
					color: #999;
					font-weight: 500;
					display: none;
					font-size: 12px;
				}
			}
			// 
			.forum_item_btn_box {
				// 
				.forum_edit_btn {
					border: 0px solid rgba(2,102,181,.5);
					cursor: pointer;
					border-radius: 4px;
					padding: 0 24px;
					margin: 0 10px 0 0;
					outline: none;
					color: rgba(2,102,181,1);
					background: none;
					width: auto;
					font-size: 14px;
					height: 30px;
				}
				// -
				.forum_edit_btn:hover {
				}
				// 
				.forum_del_btn {
					border: 0px solid #cc000050;
					cursor: pointer;
					border-radius: 4px;
					padding: 0 24px;
					margin: 0 10px 0 0;
					outline: none;
					color: #c00;
					background: none;
					width: auto;
					font-size: 14px;
					height: 30px;
				}
				// -
				.forum_del_btn:hover {
				}
			}
			// 
			.forum_time {
				color: #999;
				font-size: 14px;
			}
		}

		.forum_item:hover {
			color: #333;
			background: none;
		}
	}

	//
	.forum_detail {
		width: 100%;

		.forum_title {
			margin: 20px 0 0;
			color: #333;
			font-weight: 500;
			width: 100%;
			font-size: 16px;
			text-align: center;
		}

		.forum_info {
			padding: 0 20px;
			margin: 20px 0;
			color: #999;
			width: 100%;
			font-size: 14px;
			text-align: right;
			.forum_name {
				border: 0px solid #f5f5f5;
				padding: 0 4px 0 0;
				border-width: 0 0px 0 0;
			}
			.forum_time {
				padding: 0 0 0 4px;
			}
		}

		.forum_content {
			border: 0px solid #eee;
			padding: 0 20px;
			color: #666;
			background: none;
			width: 100%;
			font-size: 14px;
			text-align: left;
		}
	}
	// 
	.forum_comment_box {
		border: 0px solid #ddd;
		box-shadow: none;
		padding: 0;
		margin: 20px 0;
		// 
		:deep(.comment_divider) {
			border: none;
			background: #dcdfe6;
			// 
			.el-divider__text {
				font-size: 16px;
			}
		}
		// 
		.comment_add_box {
			text-align: right;
			// 
			.comment_add {
				border: 0;
				cursor: pointer;
				border-radius: 2px;
