<template>
	<div class="app-contain" :style='{"padding":"0 10%","margin":"40px auto 40px","color":"#666","background":"#fff","width":"100%","fontSize":"14px","position":"relative"}'>
		<div class="section_title">
			{{formName}}
		</div>
		<el-tabs v-model="orderStatus" type="card" class="demo-tabs" @tab-change="statusChange">
			<el-tab-pane label="" :name="''"></el-tab-pane>
			<el-tab-pane label="" name=""></el-tab-pane>
			<el-tab-pane label="" name=""></el-tab-pane>
			<el-tab-pane label="" name=""></el-tab-pane>
			<el-tab-pane label="" name=""></el-tab-pane>
			<el-tab-pane label="" name=""></el-tab-pane>
		</el-tabs>
		<el-table v-loading="listLoading" border :stripe='false' @selection-change="handleSelectionChange" ref="table"
			:data="list" @row-click="listChange">
			<el-table-column type="selection" width="55" :resizable='true' align="left" header-align="left" />
			<el-table-column label="" width="120" :resizable='true' align="left" header-align="left">
				<template #default="scope">{{ scope.$index + 1}}</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					{{scope.row.orderid}}
				</template>
			</el-table-column>
			<el-table-column label="" width="200px" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					<div style="display: flex;align-items: center;">
						<div v-if="scope.row.picture">
							<el-image v-if="scope.row.picture.substring(0,4)=='http'" preview-teleported
								:preview-src-list="[scope.row.picture.split(',')[0]]"
								:src="scope.row.picture.split(',')[0]" style="width:100px;height:100px"></el-image>
							<el-image v-else preview-teleported
								:preview-src-list="[$config.url+scope.row.picture.split(',')[0]]"
								:src="$config.url+scope.row.picture.split(',')[0]" style="width:100px;height:100px">
							</el-image>
						</div>
						<div v-else></div>
						<span style="margin-left: 10px;">{{scope.row.goodname}}</span>
					</div>
				</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					{{scope.row.buynumber}}
				</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					<span v-if="scope.row.type!=2" style="font-size: 12px;"></span>{{scope.row.price}} <span
						v-if="scope.row.type==2"></span>
				</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					<span v-if="scope.row.type!=2" style="font-size: 12px;"></span>{{scope.row.total}} <span
						v-if="scope.row.type==2"></span>
				</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					{{scope.row.address}}
				</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					{{scope.row.tel}}
				</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					{{scope.row.consignee}}
				</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					{{scope.row.remark}}
				</template>
			</el-table-column>

			<el-table-column label="" :resizable='true' align="left" header-align="left">
				<template #default="scope">
					{{scope.row.addtime}}
				</template>
			</el-table-column>
			<el-table-column label="" :resizable='true' align="left" header-align="left" width="200px">
				<template #default="scope">
					<el-button class="pay_btn" v-if="scope.row.status==''" type="primary" @click="payClick(scope.row)">
						
					</el-button>
					<el-button class="cancel_btn" v-if="scope.row.status==''" type="danger" @click="cancelClick(scope.row)">
						
					</el-button>
					<el-button class="exchange_btn" v-if="scope.row.status==''" type="primary" @click="exchange(scope.row)">
						
					</el-button>
					<el-button class="refundPrice_btn" v-if="scope.row.status==''" type="danger" @click="refundPriceClick(scope.row)">
						
					</el-button>
					<el-button class="confirm_btn" v-if="scope.row.status==''" type="success" @click="confirmGoodClick(scope.row)">
						
					</el-button>
					<el-button class="toDetail_btn" v-if="scope.row.status==''" type="warning" @click="toDetailClick(scope.row)">
						
					</el-button>
				</template>
			</el-table-column>
		</el-table>
		<el-pagination
			background 
			:layout="layouts.join(',')"
			:total="total" 
			:page-size="listQuery.limit"
			prev-text="<"
			next-text=">"
			:hide-on-single-page="false"
			:style='{"border":"0px solid #eee","padding":"4px 0","margin":"10px 0 20px","whiteSpace":"nowrap","color":"#333","textAlign":"center","flexWrap":"wrap","background":"none","display":"flex","width":"100%","fontWeight":"500","justifyContent":"center"}'
			@size-change="sizeChange"
			@current-change="currentChange" 
			@prev-click="prevClick"
			@next-click="nextClick"  />
	</div>
</template>

<script setup>
	import {
		ref,
		getCurrentInstance,
		nextTick,
	} from 'vue';
	import {
		ElMessageBox
	} from 'element-plus'
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const router = useRouter()
	const route = useRoute()
	//
	const tableName = 'orders'
	const formName = ''
	const table = ref(null)
	const selRows = ref([])
	const list = ref([])
	const listLoading = ref(false)
	const listQuery = ref({
		page: 1,
		limit: 20,
		userid: context?.$toolUtil.storageGet('userid')
	})

	//
	const handleSelectionChange = (e) => {
		selRows.value = e
	}
	//
	const getList = () => {
		listLoading.value = true
		let params = JSON.parse(JSON.stringify(listQuery.value))
		if (orderStatus.value) {
			params['status'] = orderStatus.value
		}
		context?.$http({
			url: 'orders/list',
			method: 'get',
			params: params
		}).then(res => {
			listLoading.value = false
			list.value = res.data.data.list
			total.value = Number(res.data.data.total)
		})
	}
	//
	const layouts = ref(["prev","pager","next"])
	const total = ref(0)
	const sizeChange = (size) => {
		listQuery.value.limit = size
		getList()
	}
	const currentChange = (page) => {
		listQuery.value.page = page
		getList()
	}
	const prevClick = () => {
		listQuery.value.page = listQuery.value.page - 1
		getList()
	}
	const nextClick = () => {
		listQuery.value.page = listQuery.value.page + 1
		getList()
	}
	//
	//
	const orderStatus = ref('')
	//tab
	const statusChange = () => {
		listQuery.value.page = 1
		getList()
	}
	//
	const payClick = (row) => {
		ElMessageBox.confirm(``, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning',
		}).then(() => {
			context?.$http({
				url: `${row.tablename}/info/${row.goodid}`,
				method: 'get'
			}).then(res => {
				let data = res.data.data
				if (userinfo.value.money < row.total) {
					context?.$toolUtil.message('', 'error')
					return
				}
				//
				if (data.jf) {
					userinfo.value.jf = parseInt(userinfo.value.jf) + parseInt(row.total)
				}
				//
				userinfo.value.money = (parseFloat(userinfo.value.money) - parseFloat(row.total)).toFixed(2)
				//
				if (data.alllimittimes) {
					data.alllimittimes = parseInt(data.alllimittimes) - parseInt(row.buynumber)
					//
					context?.$http({
						url: `${row.tablename}/update`,
						method: 'post',
						data: data
					}).then(obj1 => {})
				}
				//
				context?.$http({
					url: `${context?.$toolUtil.storageGet('frontSessionTable')}/update`,
					method: 'post',
					data: userinfo.value
				}).then(obj => {
					row.status = ''
					//
					context?.$http({
						url: 'orders/update',
						method: 'post',
						data: row
					}).then(res1 => {
						context?.$toolUtil.message('', 'success', () => {
							getSession()
							statusChange()
						})
					})
				})
			})
		})
	}
	//
	const cancelClick = (row) => {
		ElMessageBox.confirm(``, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning',
		}).then(() => {
			//
			row.status = ''
			//
			context?.$http({
				url: 'orders/update',
				method: 'post',
				data: row
			}).then(res1 => {
				context?.$toolUtil.message('', 'success', () => {
					getSession()
					statusChange()
				})
			})
		})
	}
	// 
	const exchange = (row) => {
		ElMessageBox.confirm(``, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning',
		}).then(() => {
			// 
			row.status = ''
			// 
			context?.$http({
				url: 'orders/update',
				method: 'post',
				data: row
			}).then(res1 => {
				context?.$toolUtil.message('', 'success', () => {
					getSession()
					statusChange()
				})
			})
		})
	}
	// 
	const refundPriceClick = (row) => {
		ElMessageBox.confirm(``, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning',
		}).then(() => {
			context?.$http({
				url: `${row.tablename}/info/${row.goodid}`,
				method: 'get'
			}).then(res => {
				let data = res.data.data
				// 
				if (data.alllimittimes) {
					data.alllimittimes = parseInt(data.alllimittimes) + parseInt(row.buynumber)
					// 
					context?.$http({
						url: `${row.tablename}/update`,
						method: 'post',
						data: data
					}).then(obj1 => {})
				}
				if (row.type == 2) {
					// 
					userinfo.value.jf = parseInt(userinfo.value.jf) + parseInt(row.total)
					// 
					context?.$http({
						url: `${context?.$toolUtil.storageGet('frontSessionTable')}/update`,
						method: 'post',
						data: userinfo.value
					}).then(obj => {
						row.status = ''
						// 
						context?.$http({
							url: 'orders/update',
							method: 'post',
							data: row
						}).then(res1 => {
							context?.$toolUtil.message('', 'success', () => {
								getSession()
								statusChange()
							})
						})
					})
				} else {
					// 
					if (data.jf) {
						userinfo.value.jf = parseInt(userinfo.value.jf) - parseInt(row.total)
					}
					// 
					userinfo.value.money = (parseFloat(userinfo.value.money) + parseFloat(row.total)).toFixed(2)
					// 
					context?.$http({
						url: `${context?.$toolUtil.storageGet('frontSessionTable')}/update`,
						method: 'post',
						data: userinfo.value
					}).then(obj => {
						row.status = ''
						// 
						context?.$http({
							url: 'orders/update',
							method: 'post',
							data: row
						}).then(res1 => {
							context?.$toolUtil.message('', 'success', () => {
								getSession()
								statusChange()
							})
						})
					})
				}
			})
		})
	}
	//
	const confirmGoodClick = (row) => {
		ElMessageBox.confirm(``, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning',
		}).then(() => {
			//
			row.status = ''
			//
			context?.$http({
				url: 'orders/update',
				method: 'post',
				data: row
			}).then(res1 => {
				context?.$toolUtil.message('', 'success', () => {
					getSession()
					statusChange()
				})
			})
		})
	}
	//
	const toDetailClick = (row) => {
		router.push(`/index/${row.tablename}Detail?id=${row.goodid}`)
	}
	const userinfo = ref({})
	const getSession = () => {
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			userinfo.value = res.data.data
		})
	}
	const init = () => {
		getSession()
		getList()
	}
	init()
</script>

